@import url("https://fonts.googleapis.com/css?family=Raleway|Shadows+Into+Light");
html, body {
  width: 100%;
  height: 100%;
  min-width: 390px;
  min-height: 650px;
  background-image: url("../images/background_home.jpg");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-height: 650px) {
  html, body {
    background-position: initial;
  }
}

canvas {
  position: absolute;
  width: 80% !important;
  bottom: 0;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

form label {
  color: white;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
}

form textarea, form input {
  color: white;
  border: none;
  font-family: "Raleway", sans-serif;
  font-size: 24px;
  border-bottom: 2px solid transparent;
  background-color: transparent;
  transition: border-bottom 0.2s;
  margin-bottom: 10px;
}

form textarea:focus, form input:focus {
  border: none;
  border-bottom: 2px solid white;
  outline: none;
  transition: border-bottom 0.2s;
}

button {
  width: 100%;
  font-family: "Raleway", sans-serif;
  font-size: 24px;
  border: none;
  background-color: white;
  cursor: pointer;
  box-shadow: none;
  border-radius: 0;
  transition: box-shadow 0.2s;
  transition: border-radius 0.2s;
}

button:hover {
  box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  transition: box-shadow 0.2s;
  transition: border-radius 0.2s;
}

nav {
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 10%;
  background: #292729;
  box-shadow: 0px 27px 24px 0px rgba(0, 0, 0, 0.2), 0px 40px 70px 0px rgba(0, 0, 0, 0.22);
}

nav .inner-nav {
  width: 90%;
}

nav .inner-nav ul {
  display: flex;
  flow-direction: row;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

nav .inner-nav ul li a {
  font-family: "Shadows Into Light", cursive;
  font-size: 30px;
  text-decoration: none;
  color: white;
  letter-spacing: 0;
  position: relative;
  text-transform: uppercase;
}

nav .inner-nav ul li a:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 0;
  height: 1px;
  background-color: white;
  content: "";
  transition: width 0.2s;
}

nav .inner-nav ul li a:hover:after {
  width: 100%;
}

.container {
  width: 80%;
  height: 90%;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column-reverse;
}

.container .inner-container {
  width: 100%;
  height: 80%;
  background-color: #292729;
  box-shadow: 0px 27px 24px 0px rgba(0, 0, 0, 0.2), 0px 40px 70px 0px rgba(0, 0, 0, 0.22);
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: center;
}

.container .inner-container .inner-container-left, .container .inner-container .inner-container-right {
  display: flex;
  flex: 1 0 30%;
  margin: 3%;
  background-color: #FFF;
  box-shadow: 0px 27px 24px 0px rgba(0, 0, 0, 0.2), 0px 40px 70px 0px rgba(0, 0, 0, 0.22);
  border-radius: 5px;
  z-index: 10;
  min-width: 250px;
}

.container .inner-container .inner-container-left .explanation {
  width: 100%;
  padding: 5%;
  font-family: "Raleway", sans-serif;
  font-size: 30px;
}

@media screen and (max-width: 650px) {
  .container .inner-container .inner-container-left .explanation {
    font-size: 24px;
  }
}

.container .inner-container .inner-container-right {
  flex-direction: column;
  justify-content: space-between;
}

.container .inner-container .inner-container-right .wish {
  flex: 1 0 auto;
  border-bottom: 1px #292729 solid;
}

.container .inner-container .inner-container-right .wish .quote-box {
  box-sizing: border-box;
  padding: 5%;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.container .inner-container .inner-container-right .wish .quote-box .quote-text {
  width: 100%;
  font-family: "Raleway", sans-serif;
  font-size: 30px;
}

.container .inner-container .inner-container-right .wish .quote-box .quote-text i {
  font-size: 1.0em;
  margin-right: 0.4em;
}

@media screen and (max-width: 650px) {
  .container .inner-container .inner-container-right .wish .quote-box .quote-text {
    font-size: 24px;
  }
}

.container .inner-container .inner-container-right .wish .quote-box .quote-author {
  font-family: "Raleway", sans-serif;
  font-style: italic;
  padding-top: 20px;
  font-size: 1em;
  text-align: right;
}

.container .inner-container .card {
  max-height: 200px;
  width: 400px;
  display: flex;
  background-color: #FFF;
  margin: 2%;
  box-shadow: 0px 27px 24px 0px rgba(0, 0, 0, 0.2), 0px 40px 70px 0px rgba(0, 0, 0, 0.22);
  border-radius: 5px;
  position: relative;
}

@media screen and (max-width: 815px) {
  .container .inner-container .card {
    margin: 4%;
  }
}

.container .inner-container .card .card_handle {
  position: absolute;
  height: 50px;
  width: 50px;
  left: 168px;
  top: -25px;
  border-radius: 50%;
  border: 6px solid white;
}

.container .inner-container .card .card_text {
  display: flex;
  flex: 1;
  z-index: 10;
}

.container .inner-container .card .card_text .quote-box {
  box-sizing: border-box;
  padding: 5%;
  width: 100%;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
  hyphens: auto;
}

.container .inner-container .card .card_text .quote-box .quote-text {
  width: 100%;
  font-family: "Raleway", sans-serif;
  font-size: 30px;
}

.container .inner-container .card .card_text .quote-box .quote-text i {
  font-size: 1.0em;
  margin-right: 0.4em;
}

.container .inner-container .card .card_text .quote-box .quote-author {
  font-family: "Raleway", sans-serif;
  font-style: italic;
  padding-top: 20px;
  font-size: 1em;
  text-align: right;
}

.container .inner-container .wish-confirm {
  width: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
}

.container .inner-container .wish-confirm .wish-confirm-inner {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.container .inner-container .wish-confirm .wish-confirm-inner h2, .container .inner-container .wish-confirm .wish-confirm-inner p, .container .inner-container .wish-confirm .wish-confirm-inner button {
  width: 30%;
}

.container .inner-container .wish-confirm .wish-confirm-inner h2 {
  color: white;
  font-family: "Shadows Into Light", cursive;
  font-size: 40px;
  text-transform: uppercase;
}

.container .inner-container .wish-confirm .wish-confirm-inner p {
  color: white;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
}

.container .inner-container .wish-confirm .wish-confirm-inner button {
  font-family: "Raleway", sans-serif;
  font-size: 24px;
  border: none;
  background-color: white;
  cursor: pointer;
  box-shadow: none;
  border-radius: 0;
  transition: box-shadow 0.2s;
  transition: border-radius 0.2s;
}

.container .inner-container .wish-confirm .wish-confirm-inner button:hover {
  box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  transition: box-shadow 0.2s;
  transition: border-radius 0.2s;
}

.container .inner-container .calibration {
  width: 100%;
  padding: 3%;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
}

.container .inner-container .calibration .calibration-inner {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.container .inner-container .calibration .calibration-inner .preset-container {
  display: flex;
  flex-direction: column;
}

.container .inner-container .calibration .calibration-inner .preset-container .preset-curr {
  color: white;
  font-family: "Raleway", sans-serif;
  font-size: 30px;
}

.container .inner-container .calibration .calibration-inner .preset-container .preset-container-inner {
  display: flex;
  justify-content: space-between;
}

.container .inner-container .calibration .calibration-inner .preset-container .preset-container-inner span, .container .inner-container .calibration .calibration-inner .preset-container .preset-container-inner form {
  width: 25%;
  margin: 0;
  padding: 0;
}

.container .inner-container .calibration .calibration-inner .preset-container .preset-container-inner span {
  color: white;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
}

.container .inner-container .calibration .calibration-inner .preset-container .preset-container-inner form button {
  background-color: transparent;
}

.container .inner-container .calibration .calibration-inner .preset-container .preset-container-inner form button i {
  color: white;
}

.container .inner-container .calibration .calibration-inner .preset-calibrate h1 {
  color: white;
  font-family: "Raleway", sans-serif;
}

.container .inner-container .wish-blow {
  display: flex;
}

.container .inner-container .wish-blow .wish-end {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5%;
}

.container .inner-container .wish-blow .wish-end p {
  color: white;
  font-family: "Raleway", sans-serif;
  font-size: 40px;
  text-align: center;
}

.container .inner-container .wish-blow .wish-end span {
  color: white;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
}

.container .pagination {
  width: 100%;
  background-color: #292729;
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 815px) {
  .container .pagination {
    padding-bottom: 5%;
  }
}

.container .pagination ul {
  width: 20%;
  margin: 1%;
  padding: 0;
  display: flex;
  justify-content: space-around;
}

.container .pagination ul li {
  list-style-type: none;
  width: 10%;
  display: flex;
  justify-content: center;
}

.container .pagination ul li:hover a {
  font-size: 24px;
  transition: font-size 0.2s;
}

.container .pagination ul li a {
  position: absolute;
  text-decoration: none;
  color: black;
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  transition: font-size 0.2s;
  color: white;
}

.container .pagination ul .active span, .container .pagination ul .disabled span {
  text-decoration: none;
  color: white;
  font-family: "Raleway", sans-serif;
  font-size: 18px;
}

.blowdiv {
  position: absolute;
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0), white);
  background: -o-linear-gradient(top, rgba(255, 255, 255, 0), white);
  background: -moz-linear-gradient(top, rgba(255, 255, 255, 0), white);
  background: linear-gradient(to top, rgba(255, 255, 255, 0) 0%, white 50%);
  width: 100%;
  height: 200%;
  z-index: 10;
  margin-top: 0;
  left: 0;
  top: 0;
  opacity: 1;
  transition: 1s;
}
